.ico {
	display: inline-block;
}

.ico.i-ascending, .ico.i-up {
	vertical-align: top;
	font-size: 0;
}

.ico.i-ascending:before, .ico.i-up:before {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 0;
	height: 0;
	margin-top: -3px;
	margin-left: -4px;
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
	border-bottom: 5px solid #b2b2b2;
	content: "";
}

.ico.i-descending, .ico.i-down {
	vertical-align: top;
	font-size: 0;
}

.ico.i-descending:before, .ico.i-down:before {
	position: absolute;
	top: 50%;
	left: 50%;
	width: 0;
	height: 0;
	margin-top: -3px;
	margin-left: -4px;
	border-left: 4px solid transparent;
	border-right: 4px solid transparent;
	border-top: 5px solid #b2b2b2;
	content: "";
}

