.mypage .datepicker {
	position: relative;
	padding: 12px 0;
	border-bottom: 2px solid rgba(224, 224, 224, 0.5);
	text-align: center;
}

.mypage .datepicker h3 {
	font-size: 16px;
	font-weight: bold;
}

.mypage .datepicker .btn {
	position: absolute;
	left: 20%;
	top: 50%;
	transform: translateY(-50%);
	min-width: 20px;
	height: 20px;
	border: 0;
	background: url("../../../img/common/btn-20-left-arrow-n@3x.png") no-repeat center;
	background-size: contain;
}

.mypage .datepicker .btn:disabled {
	opacity: 0.2;
}

.mypage .datepicker .btn-next {
	left: auto;
	right: 20%;
	background-image: url("../../../img/common/btn-20-right-arrow-n@3x.png");
}

.mypage .datepicker {
	margin: 2px 0 30px 0;
	padding: 0;
